.Backdrop,
.BackdropOpen {
  position: absolute;
  top: 0;
  left: 0;

  overflow: hidden;

  width: 100%;
  height: 100%;

  opacity: 0;
  pointer-events: none;
  transform: scale(0);
  transition: all var(--timing-function__main);
}
.BackdropOpen {
  z-index: 5;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: scale(1);
  background-color: rgba(20, 20, 20, 0.3);
}

.Container {
  position: absolute;

  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;

  text-align: center;
  padding: 4px;
  max-height: 99%;
  width: 99%;
  color: #242424;
  border-radius: 4px;
  background-color: rgba(254, 254, 254, 0.8);
  box-shadow: 0 0 8px rgba(122, 122, 122, 0.8);

  transition: all var(--timing-function__main);

  top: 5%;
  left: 50%;
  transform: translateX(-50%);
