.header,
.headerWithSearch {
  display: grid;

  grid-template-rows: 30px;
  align-content: center;
  align-items: center;
  gap: 8px;

  position: sticky;
  top: 0;
  left: 0;
  z-index: 6;

  width: 100%;
  padding: 0 8px;

  overflow: visible;
}
.Light {
  background-color: var(--brand_clr__2_light);
  color: var(--brand_clr__2);
  fill: var(--brand_clr__2);
  // border-bottom-color: var(--brand_clr__2_light);
  // box-shadow: var(--block-shadow);
}
.Dark {
  background-color: var(--brand_clr__2);
  color: #f0f0f0;
  fill: #f0f0f0;
  // border-bottom-color: var(--brand_clr__2);
}
.header {
  grid-template-columns: min-content 1fr 30px;

  @media screen and (min-width: 768px) {
    grid-template-columns: min-content 1fr 30px 30px;
  }
}
.headerWithSearch {
  grid-template-columns: min-content 1fr 30px;
  @media screen and (min-width: 768px) {
    grid-template-columns: min-content 1fr 30px 30px;
  }
}
.blockLink {
  white-space: nowrap;

  color: #fff;

  color: var(--brand_clr__1);
}
.link {
  &:hover,
  &:focus {
    outline-style: none;
  }
}
.title {
  padding: 4px;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: capitalize;
}
.btn {
  padding: 0;
  &:hover {
    fill: var(--brand_clr__1);
    background-color: rgba(254, 254, 254, 0.2);
  }
  &:active {
    fill: var(--brand_clr__1);
    background-color: rgba(254, 254, 254, 0.1);
  }
}
